.bart-shell {
  position: relative;
  width: 300px;
  height: 250px;
  z-index: 1;
  border-radius: 3px;
  overflow: hidden;
}

.pre-media-layer {
  background-color: rgba(24,24,24,100);
  -webkit-transition: opacity .4s ease-out;
  -moz-transition: opacity .4s ease-out;
  -ms-transition: opacity .4s ease-out;
  -o-transition: opacity .4s ease-out;
  transition: opacity .4s ease-out;
  z-index: 3;
}

.hidden {
  opacity: 0;
}

.visible {
  opacity: 1;
}

.media-layer {
  background-color: black;
  z-index: 2;
}

.bart-main-media {
  width: 300px;
  height: 250px;
}

.controls-layer {
  z-index: 3;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 63px;
  display: flex;
  flex-direction: column;
  align-items: stretch;
}

.main-controls {
  -webkit-transition: transform .4s ease-out;
  -moz-transition: transform .4s ease-out;
  -ms-transition: transform .4s ease-out;
  -o-transition: transform .4s ease-out;
  transition: transform .4s ease-out;
  display: flex;
  justify-content: space-between;
  flex-grow: 1;
  margin: 0 16px;
}

.fill {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.overlay-mode {
  transform: translate(0,0);
}

.player-mode {
  transform: translate(0, 17px);
}

.play-icon-container { 
  -webkit-transition: filter .15s ease-out;
  -moz-transition: filter .15s ease-out;
  -ms-transition: filter .15s ease-out;
  -o-transition: filter .15s ease-out;
  transition: filter .15s ease-out;
  cursor: pointer;
  background-color: white;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.main-controls.player-mode > .play-icon-container{
  opacity: 25%;
}

.main-controls.player-mode > .volume-icon-container {
  opacity: 25%;
}

.play-icon-container:hover {
  filter: brightness(90%);
}

.volume-icon-container {
  cursor: pointer;
  -webkit-transition: opacity .15s ease-out;
  -moz-transition: opacity .15s ease-out;
  -ms-transition: opacity .15s ease-out;
  -o-transition: opacity .15s ease-out;
  transition: opacity .15s ease-out;
  opacity: 50%;
  border: 2px solid hsla(0,0%,100%,.5);
  width: 32px;
  height: 32px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.volume-icon-container:hover {
  opacity: 100%
}

.progress-bar-container {
  -webkit-transition: transform .4s ease-out;
  -moz-transition: transform .4s ease-out;
  -ms-transition: transform .4s ease-out;
  -o-transition: transform .4s ease-out;
  transition: transform .4s ease-out;
  margin: 10px 8px 12px 8px;
  height: 3px;
  border-radius: 2px;
  overflow: hidden;
  position: relative;
}
.progress-bar {
  background-color: hsla(0,0%,100%,.3);
  height: 3px;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
}

.progress-bar-complete {
  background-color: #FF0800;
  height: 3px;
  top: 0;
  left: 0;
  border-radius: 2px;
}
.progress-bar-summary {
  -webkit-transition: opacity .4s ease-out;
  -moz-transition: opacity .4s ease-out;
  -ms-transition: opacity .4s ease-out;
  -o-transition: opacity .4s ease-out;
  transition: opacity .4s ease-out;
  color: hsla(0,0%,100%,.7);
  font-weight: 500;
  font-family: 'Netflix Sans';
  align-self: end;
}

.title-logo {
  height: 20px;
  transform: translate(9px, -30px);
}

.shirt-icon-container {
  opacity: 25%;
  cursor: pointer;
  background-color: white;
  width: 36px;
  height: 36px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;

}

.shirt-icon-container .focused {
  opacity: 100%
}